import { NgModule } from "@angular/core";
import { Route, RouterModule } from "@angular/router";
import { CreateSiniestroComponent } from "./pages/create-siniestro/create-siniestro.component";
import { ListSiniestrosComponent } from "./pages/list-siniestros/list-siniestros.component";

const routes: Route[] = [
  {
    path: "",
    redirectTo: "list-siniestros",
  },
  {
    path: "list-siniestros",
    component: ListSiniestrosComponent,
  },
  {
    path: "create-siniestros",
    component: CreateSiniestroComponent,
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class SiniestroRoutingModule {}
