import { Component, Input, OnInit } from "@angular/core";
import {
  ControlContainer,
  FormGroup,
  FormGroupDirective,
} from "@angular/forms";
import Swal from "sweetalert2";

@Component({
  selector: "app-titular-data",
  templateUrl: "./titular-data.component.html",
  styleUrls: ["./titular-data.component.scss"],
  viewProviders: [
    {
      provide: ControlContainer,
      useExisting: FormGroupDirective,
    },
  ],
})
export class TitularDataComponent implements OnInit {
  @Input() principalForm: FormGroup;

  constructor() {}

  person = {
    identification: "1250599436",
    dateBirth: new Date("10/09/2000"),
    names: "Andy Joel",
    surname: "Ninasunta Rodríguez",
    direction: "Parroquia NID. Calle 5ta ",
    phone: "0982804639",
    email: "andy@gmail.com",
    city: "Quevedo",
  };

  ngOnInit(): void {}

  validateIdentification() {
    if (
      this.principalForm.get("identification").value ===
      this.person.identification
    ) {
      this.principalForm.patchValue(this.person);
    } else {
      this.principalForm.reset("");
      this.swalInfo();
    }
  }

  swalInfo() {
    Swal.fire({
      icon: "info",
      title: "Oops...",
      text: "No se encontró la identificación!",
    });
  }
}
