import { FlatTreeControl } from "@angular/cdk/tree";
import { Component, Injectable, Input, OnInit } from "@angular/core";
import { exampleDataSiniestro } from "src/app/shared/class/data";
import {
  MatTreeFlatDataSource,
  MatTreeFlattener,
} from "@angular/material/tree";
import { MatTableDataSource } from "@angular/material/table";

@Component({
  selector: "app-table-siniestros",
  templateUrl: "./table-siniestros.component.html",
  styleUrls: ["./table-siniestros.component.scss"],
  providers: [],
})
export class TableSiniestrosComponent implements OnInit {
  data = exampleDataSiniestro;
  dataSourceAux = new MatTableDataSource();
  auxData = this.data;

  @Input() set searchFilter(text: string) {
    // console.log(text);
    this.dataSourceAux.filter = text.trim().toLowerCase();
    this.dataSource.data = this.dataSourceAux.data;
    // console.log(this.dataSourceAux.data);
  }

  pageLength = this.data.length;
  pageSize = 10;

  constructor() {
    this.dataSource.data = this.data;
    this.dataSourceAux.data = this.data;
  }

  ngOnInit(): void {}

  ngAfterViewInit() {}

  displayedColumns: string[] = [
    "aviso",
    "poliza",
    "ramo",
    "client",
    "cuenta",
    "asesor",
    "estado",
    "fecha",
    "avance",
    "acciones",
  ];

  private transformer = (node: any, level: number) => {
    return {
      expandable: !!node.children && node.children.length > 0,
      aviso: node.aviso,
      poliza: node.poliza,
      ramo: node.ramo,
      client: node.client,
      cuenta: node.cuenta,
      asesor: node.asesor,
      codEstado: node.codEstado,
      estado: node.estado,
      fecha: node.fecha,
      avance: node.avance,
      imgClient: node.imgClient,
      imgAsesor: node.imgAsesor,
      level: level,
    };
  };

  treeControl = new FlatTreeControl<any>(
    (node) => node.level,
    (node) => node.expandable
  );

  treeFlattener = new MatTreeFlattener(
    this.transformer,
    (node) => node.level,
    (node) => node.expandable,
    (node) => node.children
  );

  dataSource = new MatTreeFlatDataSource(this.treeControl, this.treeFlattener);

  hasChild = (_: number, node: any) => node.expandable;

  verData(eve: any) {
    console.log(eve);
  }
}
