import { Component, EventEmitter, OnInit, Output } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { Router } from "@angular/router";
import * as moment from "moment";
import { DropzoneConfigInterface } from "ngx-dropzone-wrapper";
import { exampleDataSiniestro } from "src/app/shared/class/data";

@Component({
  selector: "app-form-siniestro",
  templateUrl: "./form-siniestro.component.html",
  styleUrls: ["./form-siniestro.component.scss"],
})
export class FormSiniestroComponent implements OnInit {
  data = exampleDataSiniestro;

  @Output() saveEvent: EventEmitter<boolean> = new EventEmitter<boolean>();

  titularDataForm: FormGroup;
  dataVictimForm: FormGroup;
  siniestreForm: FormGroup;
  documentsForm: FormGroup;
  heading = "Bandeja de Siniestros";
  subheading = "Aqui puedes revisar todos tus siniestros registrados.";
  icon = "bg-plum-plate icon icon-gradient pe-7s-wallet";

  public config: DropzoneConfigInterface = {
    clickable: true,
    maxFiles: 10,
    autoReset: null,
    errorReset: null,
    cancelReset: null,
  };

  constructor(private formBuilder: FormBuilder, private router: Router) {}

  ngOnInit(): void {
    this.titularDataForm = this.createSiniestroForm();
    this.dataVictimForm = this.createTitularForm();
    this.siniestreForm = this.createSiniestreForm();
    this.documentsForm = this.createDocumentForm();
  }

  createSiniestroForm(): FormGroup {
    return this.formBuilder.group({
      identification: [{ value: "", disabled: false }, [Validators.required]],
      dateBirth: [{ value: "", disabled: false }, [Validators.required]],
      names: [{ value: "", disabled: false }, [Validators.required]],
      surname: [{ value: "", disabled: false }, [Validators.required]],
      direction: [{ value: "", disabled: false }, [Validators.required]],
      phone: [{ value: "", disabled: false }, [Validators.required]],
      email: [{ value: "", disabled: false }, [Validators.required]],
      city: [{ value: "", disabled: false }, [Validators.required]],
    });
  }

  createTitularForm(): FormGroup {
    return this.formBuilder.group({
      identification: [{ value: "", disabled: false }, [Validators.required]],
      dateBirth: [{ value: "", disabled: false }, [Validators.required]],
      names: [{ value: "", disabled: false }, [Validators.required]],
      surname: [{ value: "", disabled: false }, [Validators.required]],
      direction: [{ value: "", disabled: false }, [Validators.required]],
      phone: [{ value: "", disabled: false }, [Validators.required]],
      email: [{ value: "", disabled: false }, [Validators.required]],
      city: [{ value: "", disabled: false }, [Validators.required]],
    });
  }

  createSiniestreForm(): FormGroup {
    return this.formBuilder.group({
      siniestre: this.formBuilder.group({
        dateCurrent: [{ value: "", disabled: false }, [Validators.required]],
        city: [{ value: "", disabled: false }, [Validators.required]],
        description: [{ value: "", disabled: false }, [Validators.required]],
      }),
      contact: this.formBuilder.group({
        identification: [{ value: "", disabled: false }, [Validators.required]],
        dateBirth: [{ value: "", disabled: false }, [Validators.required]],
        names: [{ value: "", disabled: false }, [Validators.required]],
        surname: [{ value: "", disabled: false }, [Validators.required]],
        parent: [{ value: "", disabled: false }, [Validators.required]],
        direction: [{ value: "", disabled: false }, [Validators.required]],
        phone: [{ value: "", disabled: false }, [Validators.required]],
        email: [{ value: "", disabled: false }, [Validators.required]],
        city: [{ value: "", disabled: false }, [Validators.required]],
      }),
    });
  }
  createDocumentForm(): FormGroup {
    return this.formBuilder.group({
      doc: [{ value: "", disabled: false }, []],
    });
  }

  public onUploadInit(args: any): void {
    console.log("onUploadInit:", args);
  }

  public onUploadError(args: any): void {
    console.log("onUploadError:", args);
  }

  public onUploadSuccess(args: any): void {
    console.log("onUploadSuccess:", args);
  }


  saveSiniestro() {
    exampleDataSiniestro.push({
      aviso: "VID-00" + Number(exampleDataSiniestro.length + 1),
      poliza: "S6" + Number(exampleDataSiniestro.length + 1),
      ramo: "Asistencia",
      imgRamo: "",
      client:
        this.titularDataForm.get("names").value +
        " " +
        this.titularDataForm.get("surname").value,
      imgClient: "./assets/images/avatars/3.jpg",
      cuenta: "Banco Pichincha",
      asesor: "Ximena Rosas",
      imgAsesor: "./assets/images/avatars/1.jpg",
      codEstado: "P",
      estado: "En Proceso",
      fecha: moment().format("DD-MM-YYYY"),
      avance: 1,
      children: [],
    });

    this.saveEvent.emit(true);
  }


  goPage() {
    this.router.navigate(["/siniestros/list-siniestros"]);
  }
}
