import { Component, Input, OnInit } from "@angular/core";
import {
  ControlContainer,
  FormControl,
  FormGroup,
  FormGroupDirective,
} from "@angular/forms";
import Swal from "sweetalert2";

@Component({
  selector: "app-damaged-data",
  templateUrl: "./damaged-data.component.html",
  styleUrls: ["./damaged-data.component.sass"],
  viewProviders: [
    {
      provide: ControlContainer,
      useExisting: FormGroupDirective,
    },
  ],
})
export class DamagedDataComponent implements OnInit {
  @Input() principalForm: FormGroup;

  @Input() titularForm: FormGroup;

  switchControl: FormControl = new FormControl({
    value: false,
    disabled: false,
  });

  person = {
    identification: "1250599436",
    dateBirth: new Date("10/09/2000"),
    names: "Andy Joel",
    surname: "Ninasunta Rodríguez",
    direction: "Parroquia NID. Calle 5ta ",
    phone: "0982804639",
    email: "andy@gmail.com",
    city: "Quevedo",
  };

  constructor() {}

  ngOnInit(): void {
    this.switchControl.valueChanges.subscribe((res) => {
      if (res) {
        this.principalForm.patchValue(this.titularForm.getRawValue());

        this.principalForm.get("identification").disable();
        this.principalForm.get("dateBirth").disable();
        this.principalForm.get("names").disable();
        this.principalForm.get("surname").disable();
        this.principalForm.get("direction").disable();
        this.principalForm.get("phone").disable();
        this.principalForm.get("email").disable();
        this.principalForm.get("city").disable();
      } else {
        this.principalForm.reset("");

        this.principalForm.get("identification").enable();
        this.principalForm.get("dateBirth").enable();
        this.principalForm.get("names").enable();
        this.principalForm.get("surname").enable();
        this.principalForm.get("direction").enable();
        this.principalForm.get("phone").enable();
        this.principalForm.get("email").enable();
        this.principalForm.get("city").enable();
      }
    });
  }

  validateIdentification() {
    if (
      this.principalForm.get("identification").value ===
      this.person.identification
    ) {
      this.principalForm.patchValue(this.person);
      this.principalForm.disable();
    } else {
      this.principalForm.reset("");
      this.principalForm.enable();
      this.swalInfo();
    }
  }

  swalInfo() {
    Swal.fire({
      icon: "info",
      title: "Oops...",
      text: "No se encontró la identificación!",
    });
  }
}
